/* Title & About */
.name-about {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#company-name {
    font-size: 80px;
    font-weight: 400;
    text-align: center;
    padding: 40px;
    padding-top: 100px;
    padding-bottom: 60px;
}

.home-about {
    display: flex;
    padding: 40px;
    padding-right: 60px;
    background-color: #2c402a;
    width: 80%;
    color: #fbf8ec;
    justify-content: center;
}

.home-about h2 {
    font-weight: 500;
    padding-left: 40px;
    white-space: nowrap;
}

#home-about-text {
    flex-direction: column;
    margin-left: 100px;
}

#home-about-text p {
    margin-bottom: 10px;
}

/* Each Company Sections */
.lawn-section,
.construction-section,
.cleaning-section {
    display: flex;
    width: 80%;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    padding: 30px;
    padding-top: 70px;
}

.home-sect-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 30px;
}

.home-h2 h2 {
    font-size: 40px;
    margin-bottom: 20px;
}

.home-icon {
    flex-direction: row;
    margin-left: 20px;
}

.cleaning-section {
    padding-bottom: 70px;
}

.business-icon {
    transition: transform 0.3s ease-in-out;
    cursor: pointer;
}

.business-icon:hover {
    transform: scale(1.2);
}

/* Footer */
footer {
    padding: 30px;
    background-color: #2c402a;
    color: #FFFAE0;
}

.main-footer {
    display: flex;
    gap: 100px;
}

.main-footer li {
    padding: 5px;
    font-weight: 300;
    list-style-type: circle;
}

.main-footer a {
    color: #FFFAE0;
    text-decoration: none;
    font-weight: 300;
    padding: 5px;
}

.main-footer a:hover {
    color: #a8b480;
}

.main-footer h2 {
    display: flex;
    justify-content: center;
    padding: 20px;
    font-weight: 400;
    font-size: 25px;
}

.main-footer h3 {
    padding-bottom: 20px;
}

.contact-us {
    display: flex;
    flex-direction: column;
}

.forms-of-payment {
    display: flex;
    flex-direction: column;
}

.follow-us {
    display: flex;
    flex-direction: column;
}

.end-end {
    display: flex;
    flex-direction: column;
    gap: 10px;
    font-weight: 300;
    color: #fdf7ef;
}

.end-1 {
    display: flex;
    justify-content: end;
    gap: 10px;
}

.end-2 {
    display: flex;
    justify-content: end;
    gap: 10px;
}


@media only screen and (max-width: 745px) {
    .home-about {
        display: flex;
        flex-direction: column;
        padding: 60px;
    }

    .home-about h2 {
        padding-left: 0px;
        text-align: center;
    }

    #home-about-text {
        flex-direction: column;
        margin-left: 0px;
    }
}

@media only screen and (max-width: 600px) {
    #company-name {
        font-size: 60px;
    }
    
    .home-icon {
        flex-direction: row;
        margin-left: 0px;
    }
    .lawn-section, .construction-section, .cleaning-section {
        display: flex;
        width: 100%;
        justify-content: center;
        align-items: center;
        margin: 0 auto; 
        padding: 30px;
        padding-top: 70px;
    }
}

@media only screen and (max-width: 495px) {
    #company-name {
        font-weight: 400;
        text-align: center;
        padding: 0px;
        padding-top: 100px;
        padding-bottom: 60px;
    }
    .business-icon {
        height: 80px;
        width: 130px;
    }
    .home-icon {
        flex-direction: row;
        margin-left:-20px;
    }
    .lawn-section, .construction-section, .cleaning-section {
        display: flex;
        width: 100%;
        padding: 10px;
        padding-top: 70px;
    }
    .cleaning-section {
        padding-bottom: 70px;
    }
    .home-sect-text {
        width: 50%;
    }
}