.plantNet {
  padding-top: 100px;
}

.plantNet h2 {
  font-family: Arial, sans-serif;
  color: #2c5d63;
}

input[type="file"] {
  margin: 10px 0;
}

.plantNet button {
  padding: 10px 20px;
  background-color: #2c5d63;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #1b3d42;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  margin-bottom: 10px;
}
